a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

h1, h2, h3, h4, p, ul {
  margin: 0;
  padding: 0;
}

button {
  border: none;
}

img {
  width: 100%;
  max-width: 100%;
  display: block;
}

body {
  color: #f0f0f0;
  background-color: #12121a;
  margin: 0;
  font-family: Fira Code, Segoe UI, sans-serif;
  line-height: 1.6;
  transition: all .3s;
}

.container {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}

@media screen and (width >= 768px) {
  .container {
    width: 768px;
    max-width: none;
  }
}

@media screen and (width >= 1200px) {
  .container {
    width: 1200px;
    max-width: none;
  }
}

.header {
  z-index: 10;
  background-color: #1e1e2f;
  padding: 1.2rem 0;
  position: sticky;
  top: 0;
  box-shadow: 0 4px 10px #0000004d;
}

.header .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header h1 {
  letter-spacing: 1px;
  color: #00bcd4;
  margin: 0;
