}

.header__wrap {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.header__btn {
  color: #12121a;
  cursor: pointer;
  background-color: #00bcd4;
  border: 2px solid #0000;
  border-radius: 6px;
  padding: .5rem 1.2rem;
  font-weight: 600;
  transition: all .3s;
  box-shadow: 0 2px 6px #00bcd466;
}

.header__btn:hover {
  color: #00bcd4;
