.blogs {
  margin-top: 2.5rem;
}

.blogs__container {
  height: 181px;
  position: relative;
}

.blogs__posts {
  flex-direction: column;
  gap: 30px;
  display: flex;
}

.blogs__post {
  z-index: 2;
  background: linear-gradient(145deg, #1f1f30, #2b2b3d);
  border-radius: 10px;
  width: 100%;
  padding: 24px;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 6px 18px #00000040;
}

.blogs__post:hover {
  border-left-color: #00acc1;
}

.blogs__title {
  color: #00bcd4;
  margin-top: 0;
  line-height: 32px;
}

.blogs__author {
  color: #f0f0f0;
  margin-top: 0;
  font-size: 18px;
  line-height: 24px;
}

.blogs__text {
  color: #f0f0f0;
}

.blogs__actions {
  gap: 10px;
  padding-top: 1rem;
  display: flex;
}

.blogs__btn {
  color: #00bcd4;
  cursor: pointer;
  fill: #00bcd4;
  background-color: #0000;
  border: 2px solid #00bcd4;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  height: 35px;
  padding: 0 15px;
  font-weight: bold;
  transition: all .3s;
  display: flex;
}

.blogs__btn:hover {
  color: #12121a;
  background-color: #00bcd4;
  transform: translateY(-1px);
}

.blogs__btn--like, .blogs__btn--dislike {
  color: #00bcd4;
  cursor: pointer;
  fill: #00bcd4;
  background-color: #0000;
  border-color: #00bcd4;
  border-radius: 4px;
  padding: .4rem 1rem;
  font-weight: bold;
  transition: all .3s;
}

.blogs__btn--like:hover, .blogs__btn--dislike:hover {
  color: #12121a;
  background-color: #00bcd4;
  transform: translateY(-1px);
}

.blogs__btn--clicked {
  color: #12121a;
  fill: #12121a;
  background-color: #00bcd4;
}

.blogs__likesvg {
  width: 18px;
  height: 18px;
  fill: inherit;
  transition: all .3s;
}

.blogs__dislikesvg {
  width: 18px;
  height: 18px;
  fill: inherit;
  transition: all .3s;
  transform: rotate(180deg);
}

.blogs__commentsvg {
  width: 18px;
  height: 18px;
  fill: inherit;
  transition: all .3s;
}

.blogs__editsvg, .blogs__deletesvg {
  fill: #00bcd4;
  width: 18px;
  height: 18px;
  transition: all .3s;
}

.blogs__btn:hover .blogs__likesvg, .blogs__btn:hover .blogs__dislikesvg, .blogs__btn:hover .blogs__commentsvg, .blogs__btn:hover .blogs__editsvg, .blogs__btn:hover .blogs__deletesvg {
  fill: #12121a;
}

.blogs__comments {
  z-index: 1;
  background: linear-gradient(0deg, #1f1f30, #4b4b71);
  border-bottom: 4px solid #00bcd4;
  border-radius: 0 0 10px 10px;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  gap: 10px;
  width: 1228px;
  max-height: 100px;
  padding: 18px 10px 10px;
  transition: all .3s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  transform: translate(0, 53px);
}

.blogs__comment {
  background-color: #0000004d;
  border-radius: 10px;
  width: calc(100% - 20px);
  padding: 10px;
}

.blogs__add {
  color: #00bcd4;
  cursor: pointer;
  background-color: #0000;
  border: 2px solid #00bcd4;
  border-radius: 4px;
  width: calc(100% + 44px);
  margin-top: 30px;
  margin-bottom: 100px;
  padding: 10px 20px;
  font-weight: bold;
  transition: all .3s;
}

.blogs__add:hover {
  color: #12121a;
  background-color: #00bcd4;
  transform: translateY(-1px);
}

.blogs__add__down {
  transition: all .3s;
}

.comments--down {
  max-height: 400px;
  transition: all .3s;
  overflow: hidden scroll;
  transform: translate(0, 170px);
}

.blogs__comments::-webkit-scrollbar {
  width: 8px;
}

.blogs__comments::-webkit-scrollbar-track {
  background: #1f1f30;
  border-radius: 10px;
}

.blogs__comments::-webkit-scrollbar-thumb {
  background-color: #00bcd4;
  border: 2px solid #1f1f30;
  border-radius: 10px;
}

.blogs__comments::-webkit-scrollbar-thumb:hover {
  background-color: #00acc1;
}

.post-down {
  transition: all .3s;
}

.add__backdrop {
  z-index: 11;
